import React, { Component } from 'react';

class NavMenu extends Component {

    constructor(props) {
        super(props);
        this.clickHamburger = this.clickHamburger.bind(this);
        this.state = {
            active: false
        };
    }

    handleEvent = event => {
        const url = 'https://drive.google.com/file/d/17QbkScF4RExOwqH9HcHT_m_FpcS4F5am/view?usp=sharing';
        window.open(url, '_blank');
    };


    clickHamburger() {
        const currentState = this.state.active;
        this.setState({ active: !currentState });
    }

    render() {
        return (

            <div className="Menu-container box">
                <nav class="navbar" role="navigation" aria-label="main navigation">
                    <div class="navbar-brand">
                        <a role="button" onClick={this.clickHamburger} className={this.state.active ? 'is-active navbar-burger burger' : 'navbar-burger burger'} aria-label="menu" aria-expanded="false" data-target="navbarBasicExample">
                            <span aria-hidden="true"></span>
                            <span aria-hidden="true"></span>
                            <span aria-hidden="true"></span>
                        </a>
                    </div>
                    <div className={this.state.active ? 'is-active navbar-menu' : 'navbar-menu'} >
                        <div class="navbar-start">
                            <a class="navbar-item is-activated" href="./">Home</a>
                            <a class="navbar-item" href="hello#About-Me">About Me</a>
                            <a class="navbar-item" href="./#My-Experiences">My Experiences</a>
                            <a class="navbar-item" href="./#My-Projects">My Projects</a>
                            <a class="navbar-item" href="./#My-Articles">My Articles</a>
                            <a class="navbar-item" href="./#My-Talks">My Talks</a>
                        </div>
                        <div class="navbar-end has-text-centered">
                            <a class="button is-my-dark is-rounded is-outlined " onClick={this.handleEvent}>
                                <strong>Download My CV</strong>
                            </a>
                        </div>
                    </div>
                </nav>
            </div>

        );
    }

}

export default NavMenu;
